import React from "react";
import "./BenefitsCard.css";
function BenefitsCard() {
  return (
    <div
      className="BenefitsCardContainer "
      style={{ backgroundColor: "#9BB3F8" }}
    >
      <div className="Content flex flex-center flex-column">
        <div className="BenefitsCardTitle title-text">Immutability</div>
        <div className="BenefitsCardContent text">
          Once data is recorded on a blockchain, it's extremely difficult to
          alter or delete. This immutability ensures a high level of trust and
          security in the information stored on the blockchain
        </div>
      </div>
    </div>
  );
}

export default BenefitsCard;
