import React from "react";
import "./OfferingsCard.css";
import Icon from "../../Assets/currency 1.svg";
function OfferingsCard() {
  return (
    <div className="OfferingCardContainer" style={{ backgroundColor: "#8C6EDE" }}>
      <div className="OfferingIcon">
        <img src={Icon} />
      </div>
      <div className="OfferingTitle title-text">Cryptocurrency Exchange Solution</div>
      <div className="OfferingData">
        A cryptocurrency exchange solution refers to a platform or software that
        enables users to buy, sell, trade, and manage various cryptocurrencies.
        These exchanges act as intermediaries that facilitate the conversion of
        digital assets and provide a marketplace for users to engage in crypto
        trading
      </div>
    </div>
  );
}

export default OfferingsCard;
